<template>
    <div class="referral-program">
        <div class="ref-title">{{$localizeFilter('refferal_program_title')}}</div>
        <div class="ref-subtitle">{{$localizeFilter('refferal_program_subtitle')}}</div>
        <div class="flex-wrapper">
            <div class="period">
                <div class="period-item">
                    <div class="item-title">{{$localizeFilter('refferal_program_day')}}</div>
                    <div class="item-subtitle">${{$utils.formatNumber(income.day || 0, 'en-US', 0, 2)}}</div>
                </div>
                <div class="period-item">
                    <div class="item-title">{{$localizeFilter('refferal_program_week')}}</div>
                    <div class="item-subtitle">${{$utils.formatNumber(income.week || 0, 'en-US', 0, 2)}}</div>
                </div>
                <div class="period-item">
                    <div class="item-title">{{$localizeFilter('refferal_program_month')}}</div>
                    <div class="item-subtitle">${{$utils.formatNumber(income.month || 0, 'en-US', 0, 2)}}</div>
                </div>
            </div>
            <div class="grid">
                <div class="period-item grid-item">
                    <div class="item-title">{{$localizeFilter('refferal_program_available')}}</div>
                    <div class="item-subtitle">${{$utils.formatNumber(income.available || 0, 'en-US', 0, 2)}}</div>
                </div>
                <div class="period-item grid-item">
                    <div class="item-title">{{$localizeFilter('refferal_program_refs')}}</div>
                    <div class="subtitle-block">
                        <div class="item-subtitle">{{referralsCount.real || 0}}</div>
                        &nbsp;
                        <div class="item-subtitle-2">{{`(+${referralsCount.virtual || 0})`}}</div>
                    </div>
                </div>
                <div class="basic-button" @click="withdraw()">{{$localizeFilter('refferal_program_withdraw')}}</div>
                <div class="grid-item">
                    <div class="grid-item-title">{{$localizeFilter('refferal_program_next_level')}}</div>
                    <div class="grid-item-subtitle">{{referralsCount.toNextLevel || 0}}</div>
                </div>
            </div>
        </div>
        <div class="content-block">
            <ReferralProgramTable :levels="levels" :style="'margin-top: 30px; margin-bottom: 30px;'"/>
            <transition name="fade">
            <div class="charts-block" v-if="showCharts">
                <ReferralProgramCharts :title="$localizeFilter('referrals')" :data="chartsData.referrals || {}"  :isReferralsChart="true" />
                <ReferralProgramCharts :title="$localizeFilter('turnover')" :data="chartsData.turnover || {}"/>
                <ReferralProgramCharts :title="$localizeFilter('income')"  :data="chartsData.income || {}"/>
            </div>
            </transition>
        </div>

        
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import ReferralProgramTable from '@/components/ReferralProgramTable.vue'
import ReferralProgramCharts from "@/components/ReferralProgramCharts.vue"

export default defineComponent({
    setup() {
    },
    components: { ReferralProgramTable, ReferralProgramCharts },
    data: ()=>({
        showCharts: false
    }),
    computed: {
        levels() {
            return this.$store.getters.referralProgramData?.levels || []
        },
        income() {
            return this.$store.getters.referralProgramData?.income || {}
        },
        referralsCount() {
            return this.$store.getters.referralProgramData?.referralsCount || {}
        },
        chartsData() {
            return this.$store.getters.referralProgramData?.chartsData || {}
        }
    },
    methods: {
        withdraw() {
            this.$router.push('/cabinet/bonusAccount?coin=USDT&withdraw=1')
        }
    },
    async mounted() {
        try {
            await this.$store.dispatch('getReferralProgramData')
            this.$nextTick(()=>{
                this.showCharts = true
            })
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

