<template>
<div class="referral-program-charts">
    <apexchart  
        class="chart"
        width="292"
        height="186"
        type="area"
        :options="options"
        :series="series"
    />
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import VueApexCharts from "vue3-apexcharts";
import dateTimeFilter from '@/filters/date.filter'


export default defineComponent({
    props:{
      data: {
        type: Object, 
        required: true
      },
      title: {
        type: String, 
        required: ''
      },
      isReferralsChart: {
        type: Boolean,
        default: false
      }
    },
    components: {
        apexchart: VueApexCharts,
    },
    
    setup(props) {
        const options = {
        chart: {
          id: 'vuechart-example',
          background: 'transparent',
          stacked: true
        },
        title: {
          text: props.title,
          align: 'left',
          margin: 8,
          offsetX: 0,
          offsetY: 14,
          floating: false,
          style: {
            fontSize:  '16px',
            fontWeight:  '600',
            fontFamily:  undefined,
            color:  '#EAEAEA'
          },
        },
        xaxis: {
          categories: props.isReferralsChart ? props.data.realReferrals.categories : props.data.categories,
          axisBorder:{
            color:'transparent'
          },
          offsetY: -5,
          labels: {
            style: {
              colors: ['#EAEAEA'],
              fontSize: '8px',
            },
            formatter: function (value) {
              if (value && value > 0) {
                return dateTimeFilter(value, 'custom', 'dd.mm');
              } else {
                return ''
              }
            }
          },
          axisTicks: { 
            color: 'transparent',
            height: 6,
            offsetX: 0,
            offsetY: 0
          },
          tooltip: {
            enabled: false
          }
        },
        tooltip: {
          enabled: false
        },
        yaxis: {
          axisBorder:{
            color:'transparent'
          },
          offsetY: -5,
          labels: {
            style: {
              colors: ['#EAEAEA'],
              fontSize: '8px',
            },
          },
          axisTicks: { 
            color: 'transparent',
            height: 6,
            offsetX: 0,
            offsetY: 0
          },
          tooltip: {
            enabled: false
          }
        },
        grid:{
            borderColor: '#EAEAEA',
        },
        colors: ['#28BEB2', '#F2B134'],
        fill: {
          type: 'gradient',
          gradient: {
            opacityFrom: 1,
            opacityTo: 0.1,
          }
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
            width: 2,
        },
      }
      const series = [{
        name: 'series-1',
        data: props.isReferralsChart ? props.data.realReferrals.series : props.data.series
      }]
      if (props.isReferralsChart) {
        series.push({
        name: 'series-2',
        data: props.data.virtualReferrals.series
      })
      }
      return {
        options, 
        series
      };
    },
})
</script>

<style scoped>
.referral-program-charts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
</style>
